'use server';

import { summarizeTerrainAnalysis } from '@/ai/flows/summarize-terrain-analysis';
import { optimizeZoneDistribution } from '@/ai/flows/optimize-zone-distribution';
import { simulateTrafficAndPollution } from '@/ai/flows/simulate-traffic-and-pollution';
import type { CityParams, CityPlan, FullReport } from '@/lib/types';

export async function analyzeImage(imageData: string): Promise<string> {
  if (!imageData) {
    throw new Error('Image data is required for analysis.');
  }

  // The provided Genkit flow requires a `terrainAnalysis` string.
  // Since we cannot generate it from the image alone with the given flows,
  // we will provide a mock, descriptive analysis to satisfy the flow's input schema.
  const mockTerrainAnalysis =
    'The satellite image displays a varied landscape. The area is predominantly flat with a gentle slope towards the south. A river meanders from the northwest to the southeast, suggesting ample water supply but also flood-prone zones. Patches of dense green indicate fertile soil, suitable for parks or agriculture. A rocky outcrop is visible in the northeast quadrant, which might be challenging for construction but could be a source of materials. There is no existing infrastructure visible.';

  try {
    const result = await summarizeTerrainAnalysis({
      satelliteImageDataUri: imageData,
      terrainAnalysis: mockTerrainAnalysis,
    });
    return result.summary;
  } catch (error) {
    console.error('Error in summarizeTerrainAnalysis flow:', error);
    throw new Error('Failed to analyze terrain from image.');
  }
}

export async function getOptimizedZones(
  terrainSummary: string,
  population: number,
  budget: number
) {
  try {
    const result = await optimizeZoneDistribution({
      terrainAnalysis: terrainSummary,
      population,
      budget,
    });
    return result;
  } catch (error) {
    console.error('Error in optimizeZoneDistribution flow:', error);
    throw new Error('Failed to optimize zone distribution.');
  }
}

export async function generatePlanAndSimulate(
  terrainSummary: string,
  cityParams: CityParams
): Promise<FullReport> {
  // Construct a mock but detailed city plan based on the user's vision.
  // The AI flows do not generate a full geometric plan, so we create a structured
  // text-based plan that can be used for the simulation step.
  const cityPlan: CityPlan = {
    overview: terrainSummary,
    roadNetwork: {
      highways: ['Connect to National Highway 1 on the north.', 'State Highway 5 connection on the west.'],
      arterialRoads: ['A 6-lane ring road encircling the city core.', '4-lane roads connecting zones.'],
      localRoads: ['Standard 2-lane local access roads for all residential and commercial blocks.'],
      reasoning: 'The road network is designed for efficient flow, with a hierarchical structure to separate local and through traffic, minimizing congestion in residential areas.',
    },
    zoning: {
      residential: {
        areas: ['Central and southern parts of the city'],
        density: 'High-density near the commercial core, medium and low-density towards the outskirts.',
        reasoning: 'Residential areas are placed away from the industrial zone to reduce pollution exposure and have easy access to green spaces and commercial districts.',
      },
      commercial: {
        areas: ['A central business district (CBD) is planned at the city\'s geometric center.'],
        reasoning: 'The CBD is centrally located for maximum accessibility. Smaller commercial hubs are planned within large residential sectors.',
      },
      industrial: {
        areas: ['The eastern quadrant, downwind from residential areas.'],
        reasoning: 'The industrial zone is placed downwind to prevent air pollution from affecting residential zones, with a dedicated highway connection for logistics.',
      },
      greenSpaces: {
        areas: ['A large central park, riverfront development, and smaller community parks.'],
        reasoning: 'Green spaces are integrated throughout the city to improve quality of life, manage stormwater, and provide recreational areas for citizens.',
      },
       publicServices: {
        areas: ['Distributed throughout the city'],
        reasoning: 'Schools, hospitals, and police stations are strategically located to ensure equitable access for all residents, with main facilities in the central district.',
      },
    },
    infrastructure: {
      powerSupply: {
        locations: ['Solar farm in the sunny, arid northwest.'],
        type: cityParams.specialRequirements.solarPower ? 'Solar and Grid' : 'Grid',
        capacity: '150MW',
        reasoning: 'A mix of renewable energy and traditional grid connection ensures a reliable and sustainable power supply.',
      },
      waterSupply: {
        treatmentPlants: ['One plant upstream of the river.'],
        pipelineNetwork: 'A city-wide grid following the main road network.',
        reasoning: 'The water treatment plant is located upstream to ensure a clean water source, with recycling facilities if specified.',
      },
      sewageSystem: {
        treatmentPlants: ['One plant downstream of the river.'],
        network: 'Gravity-fed system following natural slopes to the treatment plant.',
        reasoning: 'The sewage plant is located downstream to prevent contamination of the city\'s water supply.',
      },
    },
    budgetBreakdown: {
      landAcquisition: '20% of Budget',
      roadConstruction: '35% of Budget',
      buildingConstruction: '30% of Budget',
      utilities: '15% of Budget',
      materials: {
        concrete: '50,000 cubic meters',
        steel: '10,000 tons',
        asphalt: '100,000 tons',
        pipes: '150 km',
      },
    },
    timeline: {
      phase1: 'Year 1-2: Land acquisition and primary infrastructure (main roads, water, power).',
      phase2: 'Year 3-5: Development of commercial and industrial zones, and first residential sectors.',
      phase3: 'Year 6-10: Expansion of residential areas and public services.',
    },
  };
  
  try {
    const simulation = await simulateTrafficAndPollution({
      cityPlan: JSON.stringify({ ...cityPlan, parameters: cityParams }),
    });

    return { cityPlan, simulation, cityParams };
  } catch (error) {
     console.error('Error in simulateTrafficAndPollution flow:', error);
    throw new Error('Failed to run simulation on the city plan.');
  }
}
