// src/ai/flows/simulate-traffic-and-pollution.ts
'use server';
/**
 * @fileOverview Simulates traffic and pollution for a city plan.
 *
 * - simulateTrafficAndPollution - A function that simulates traffic and pollution based on a city plan.
 * - SimulateTrafficAndPollutionInput - The input type for the simulateTrafficAndPollution function, which includes the city plan as a JSON string.
 * - SimulateTrafficAndPollutionOutput - The return type for the simulateTrafficAndPollution function, providing an analysis of potential issues and suggestions.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const SimulateTrafficAndPollutionInputSchema = z.object({
  cityPlan: z.string().describe('The city plan as a JSON string.'),
});
export type SimulateTrafficAndPollutionInput = z.infer<typeof SimulateTrafficAndPollutionInputSchema>;

const SimulateTrafficAndPollutionOutputSchema = z.object({
  trafficAnalysis: z.string().describe('An analysis of the traffic flow in the city, identifying potential congestion points.'),
  pollutionAnalysis: z.string().describe('An analysis of the pollution impact of the city, identifying potential problem areas.'),
  potentialIssues: z.array(z.string()).describe('A list of potential issues identified by the simulation.'),
  suggestions: z.array(z.string()).describe('A list of suggestions to address the potential issues.'),
});
export type SimulateTrafficAndPollutionOutput = z.infer<typeof SimulateTrafficAndPollutionOutputSchema>;

export async function simulateTrafficAndPollution(input: SimulateTrafficAndPollutionInput): Promise<SimulateTrafficAndPollutionOutput> {
  return simulateTrafficAndPollutionFlow(input);
}

const simulateTrafficAndPollutionPrompt = ai.definePrompt({
  name: 'simulateTrafficAndPollutionPrompt',
  input: {schema: SimulateTrafficAndPollutionInputSchema},
  output: {schema: SimulateTrafficAndPollutionOutputSchema},
  prompt: `You are an expert urban planner specializing in simulating the traffic flow and pollution impact of city plans.

You will receive a city plan as a JSON string.  Your task is to analyze the plan and identify potential issues related to traffic and pollution.

Based on your analysis, provide a list of potential issues and suggestions to address them.

City Plan:
{{{cityPlan}}}

Return your response in JSON format.
`,
});

const simulateTrafficAndPollutionFlow = ai.defineFlow(
  {
    name: 'simulateTrafficAndPollutionFlow',
    inputSchema: SimulateTrafficAndPollutionInputSchema,
    outputSchema: SimulateTrafficAndPollutionOutputSchema,
  },
  async input => {
    const {output} = await simulateTrafficAndPollutionPrompt(input);
    return output!;
  }
);
